/*------------------------------------------------------------
# Base
------------------------------------------------------------*/
/**
 * Set up a decent box model on the root element
 */
html {
  box-sizing: border-box;
}

/**
  * Make all elements from the DOM inherit from the parent box-sizing
  * Since `*` has a specificity of 0, it does not override the `html` value
  * making all elements inheriting from the root box-sizing value
  * See: https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
  */
*,
*::before,
*::after {
  box-sizing: inherit;
}

img,
iframe {
  max-width: 100%;
}

/* Elements
--------------------------------------------- */
/* Scroll */
::-webkit-scrollbar {
  height: 8px;
  width: 8px;
}
main ::-webkit-scrollbar {
  height: 6px;
  width: 6px;
}

::-webkit-scrollbar-thumb {
  background: #404040;
  border-radius: 0;
}

::-webkit-scrollbar-thumb:active,
::-webkit-scrollbar-thumb:hover {
  background-color: #737373;
}

::-webkit-scrollbar-track {
  background: #d9d9d9;
}
main ::-webkit-scrollbar-track {
  background: #d9d9d9;
  border-radius: 10px;
  border: 2px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-track:hover::-webkit-scrollbar-track:hover,
::-webkit-scrollbar-track:active {
  background: #4D9EAD;
}

/* Selection */
::selection {
  background-color: #A561B7;
  color: #fff;
}

div p:last-child {
  margin-bottom: 0;
}

/* Selection */
::selection {
  background-color: #A561B7;
  color: #fff;
}

/*------------------------------------------------------------
# Layouts
------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
.main-menu {
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.1019607843);
  background-color: #fff;
  position: relative;
}
.main-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.main-menu ul li:hover, .main-menu ul li:focus {
  font-weight: 700;
}
.main-menu ul li:hover ul.main-menu-submenu, .main-menu ul li:focus ul.main-menu-submenu {
  display: block;
}
.main-menu ul.main-menu-submenu {
  background-color: #A561B7;
  color: #fff;
  position: relative;
  display: none;
  padding: 0;
  text-align: center;
}
.main-menu ul.main-menu-submenu a {
  color: #fff;
  font-weight: 400;
  line-height: 2;
  padding: 0;
}
.main-menu a {
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
}
.main-menu a:hover {
  text-decoration: none;
}
.main-menu__links {
  width: 100%;
}
.main-menu__profile svg {
  color: #A561B7;
}
.main-menu__profile ul {
  display: none;
}
.main-menu__profile ul li {
  background-color: inherit;
  position: relative;
}
.main-menu__profile ul li:hover {
  background-color: #A561B7;
  color: #fff;
}
.main-menu__profile ul li:hover svg {
  color: #fff;
}
.main-menu__profile ul li:hover a {
  background-color: #A561B7;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
}
.main-menu__profile ul li ul.main-menu-submenu {
  right: 0;
  width: 100%;
}
.main-menu__profile a {
  color: #A561B7;
  padding: 1rem;
}
.main-menu__profile a.btn-message {
  position: relative;
}
.main-menu__profile a.btn-message .notification-badge {
  display: none;
  background-color: #FF0000;
  color: #fff;
  font-size: 8px;
  text-align: center;
  height: 12px;
  width: 12px;
  position: absolute;
  top: 16px;
  right: 16px;
  border-radius: 50%;
}
.main-menu__profile a.btn-message.has-message .notification-badge {
  display: block;
}
@media screen and (min-width: 992px) {
  .main-menu ul li:hover ul.main-menu-submenu {
    left: auto;
    opacity: 1;
    z-index: 10;
    transform: rotateY(0deg);
  }
  .main-menu ul.main-menu-submenu {
    background-color: #A561B7;
    border-radius: 0px 0px 8px 8px;
    color: #fff;
    position: absolute;
    top: 100%;
    left: 0;
    display: block;
    opacity: 0;
    padding: 0.5rem 1.5rem;
    text-align: left;
  }
  .main-menu ul.main-menu-submenu a {
    color: #fff;
  }
  .main-menu ul.navbar-nav a {
    position: relative;
  }
  .main-menu ul.navbar-nav a::after {
    content: "";
    background-color: #A561B7;
    position: absolute;
    top: 75%;
    left: 0;
    height: 1px;
    width: 0%;
    transition: width 0.2s ease-in-out;
  }
  .main-menu ul.navbar-nav a:hover::after {
    width: 100%;
  }
  .main-menu ul.navbar-nav ul li:hover ul.main-menu-submenu {
    transform: rotateY(0deg);
  }
  .main-menu ul.navbar-nav ul.main-menu-submenu {
    transform: rotateY(-90deg);
    transition: transform 0.2s ease-in;
  }
  .main-menu a {
    padding: 1rem;
  }
  .main-menu__links {
    width: auto;
    margin-right: auto;
  }
  .main-menu__profile {
    order: 4;
    border-left: 2px solid #A561B7;
    width: auto;
  }
  .main-menu__profile ul {
    display: flex;
  }
}

